.formWrapper {
  width: 100%;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 10px;

  margin: 10px 0px;
}

.sizeWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.button {
  display: block;
  padding: 10px 20px;

  margin: 30px auto 0px;

  background-color: var(--text-green-color);
  border: 1px solid var(--border-color);
  border-radius: 4px;

  letter-spacing: 0.1em;
  text-transform: uppercase;

  &:active {
    background-color: rgb(35, 179, 147);
  }
}

.sizeTitle {
  margin-bottom: 5px;
}
